<template>
  <div class="container h-100">
    <h1>Internet Radio</h1>
    <div v-if="api.radio_language == 'pl'" class="row stations-container justify-content-around">
      <div v-for="station in this.pl_stations" :key="station.name">
        <img class="hover-scale d-block" :src="station.cover" height="150"
          @click="startPlaying(station.link, station.name)">
        <small>{{ station.name }}</small>
      </div>
    </div>

    <div v-if="api.radio_language == 'eng'" class="row stations-container justify-content-around">
      <div v-for="station in this.eng_stations" :key="station.name">
        <img class="hover-scale d-block" :src="station.cover" height="150"
          @click="startPlaying(station.link, station.name)">
        <small>{{ station.name }}</small>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'InternetRadio',
    data() {
      return {
        pl_stations: {
          "0": {
            "cover": 'https://static.radio.pl/images/broadcasts/ee/b3/10194/c300.png',
            "name": "RMFFMCLUB",
            "link": "http://80.48.65.99/rmf_club",
          },
          "1": {
            "cover": 'https://static.radio.fr/images/broadcasts/df/f7/10167/c300.png',
            "name": "RMFFMMAXXX",
            "link": "http://80.48.65.99/rmf_maxxx",
          },
          "2": {
            "cover": 'https://static.wirtualnemedia.pl/media/top/Antyradioliner2015655.jpg',
            "name": "AntyRadio",
            "link": "https://n-22-6.dcs.redcdn.pl/sc/o2/Eurozet/live/antyradio.livx?audio=5",
          },
        },
         eng_stations: {
          "0": {
            "cover": 'https://image.spreadshirtmedia.com/image-server/v1/compositions/15398563/views/1,width=650,height=650,appearanceId=120,version=1531916035.jpg',
            "name": "NSBRadio",
            "link": "http://live.nsbradio.co.uk:8904/",
          },
          "1": {
            "cover": 'https://cdn-profiles.tunein.com/s54426/images/logoq.png?t=636307',
            "name": "DeepRadio",
            "link": "http://stream.deep.radio/hd",
          },
          "2": {
            "cover": require('../assets/default-radio-cover.png'),
            "name": "BBCRadio",
            "link": "http://sc59.lon.llnw.net/stream/bbcmedia_radio1_mf_p",
          },
        }
      }
    },
    methods: {
      async startPlaying(file_name, radio_text) {
        this.stopPlaying();
        try {
          await this.api.startPlayingInternet(file_name, radio_text);
          this.api.status = await this.api.getStatus();
          $('.media-progress-bar').stop(true).css(
            'width', '0%'
          );
        } catch (e) {
          this.api.processException(e);
        }
      },

      async stopPlaying() {
        try {
          await this.api.stopPlaying();
          window.clearInterval(this.timer);
          $('.media-progress-bar').stop(true).css(
            'width', '0%'
          );
          this.api.status = await this.api.getStatus();
        } catch (e) {
          this.api.processException(e);
        }
      },
    },
  };

</script>

<style scoped lang="scss">
  .stations-container {
    width: 100%;
    max-height: 80%;
    overflow-y: auto;
    padding: 20px;
  }

</style>
